
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {httpUrl} from '../../http_url';

const list = () => [
{title:'登录',id:0},
{title:'注册',id:1}
]
export default {



data () {
return {
menus: {
menu1: '退出登录'
},
yhUserName:'',
password:'',
passwordAgain:'',
phone:'',
index:1,
demo1:'注册',
show:true,
time:60,
start: false,
list2:list(),
identifyCode:'',

showMenus: false,
length:'',
status_name:'',
orderid:''
}
},
created(){

},
components: {
XHeader,
PopupHeader,
Actionsheet,
Card,
Tab,
TabItem,
Group,XInput,XButton,CheckIcon,Countdown
},
methods:{
getData(id){
console.log(id);
const _this = this;
let postData = this.$qs.stringify({
status:id
});
this.$axios({
method: 'post',
url:httpUrl.txorderList,
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.signeList = res.data.retData;
this.length = res.data.retData.length;
this.status_name = res.data.retData.status_name;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',



})
}
});
},
blurPassword(){
const _this = this;
if(this.isEmpty(this.password)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(!this.Val.isPassWord(this.password) || this.password.length<6){
this.$vux.alert.show({
title: '错误提示',
content: '新密码必须为6位以上的英文和数字组成',
onHide () {
_this.password = ''
}
})
}
},
blurPasswordAgain(){
const _this = this;
if(this.isEmpty(this.passwordAgain)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(this.passwordAgain != this.password){
this.$vux.alert.show({
title: '错误提示',
content: '两次密码输入不一致',
onHide () {
_this.passwordAgain = ''
}
})
}
},

submit(){
const _this = this;
let postData = this.$qs.stringify({
userName:this.yhUserName,
code:this.identifyCode,
password:this.password,
ckpassword:this.passwordAgain,
phone:this.SpaceTransformation(this.phone),
});
this.$axios({
method: 'post',
url: '',
data: postData
}).then( (res)=>{
console.log(res.data);
if(res.data.errCode==200){
this.$router.push('/credit_login');

}else {
this.index = 1;
}
}).catch( function(){
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
})
},

LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{

if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
finish (index) {
this.start = false;
this.time = 60;
this.show = true;
},
goHome(){
this.$router.push('/home');
}
}
}





